import React, { PureComponent } from 'react';
import Button3D from '../button3D/Button3D';
import BackgroundText from '../backgroundText/BackgroundText';

export default class Home extends PureComponent {
	
	componentDidMount() {
		if(this.props.pastInitLoad) {
			setTimeout(() => {
				this.props.hideLoader();
				this.props.loaded();
			}, 500);
		}
	}

	componentWillUnmount() {
		this.props.removeHomeClasses();
	}

	render = () => {
		let inc = 7, inc2 = 0;
		return (
			<section 
				onMouseMove={this.props.handlePageMove}
				onMouseEnter={this.props.handlePageEnter}
				onTouchMove={this.props.handlePageMove}
				onTouchStart={this.props.handlePageEnter}
				className={this.props.classes}
				style={{
					backgroundPosition: `${this.props.moveX}% 100%`,
					transition: `background-position ${this.props.transDur} 0s, transform 0.5s 0s`
				}}>
				<BackgroundText
					text="Alex"
					incrementor={0} />
				<BackgroundText
					text="Figliolia"
					incrementor={0} />
				<div>
					<div>
						<h2>
							{
								'Front End'.split('').map((letter, i) => {
									if(letter === ' ') {
										return <div 
											key={i}
											className="space"
											style={{
												transitionDelay: inc/140 + 's'
											}}>&nbsp;&nbsp;</div>
									} else {
										inc-=i;
										return <div 
											key={i}
											style={{
												transitionDelay: inc/140 + 's'
											}}>{letter}</div>
									}
								})
							}
						</h2>
						<h2>
							{
								'Developer'.split('').map((letter, i) => {
									inc2 += i;
									return <div 
										key={i}
										style={{
											transitionDelay: inc2/140 + 's'
										}}>{letter}</div>
								})
							}
						</h2>
					</div>
					<Button3D 
						text="Work"
						function={this.props.nav} />
				</div>
			</section>
		);
	}
}



