import React, { PureComponent } from 'react';
import Button3D from '../button3D/Button3D';
import BackgroundText from '../backgroundText/BackgroundText';

export default class Contact extends PureComponent {

	componentDidMount() {
		setTimeout(() => {
			this.props.hideLoader();
			this.props.loaded();
		}, 500);
	}

	componentWillUnmount() {
		this.props.removeContactClasses();
	}

	render = () => {
		return (
			<section 
				onMouseMove={this.props.handlePageMove}
				onMouseEnter={this.props.handlePageEnter}
				onTouchMove={this.props.handlePageMove}
				onTouchStart={this.props.handlePageEnter}
				className={this.props.classes}
				style={{
					backgroundPosition: `${this.props.moveX}% 100%`,
					transition: `background-position ${this.props.transDur} 0s, transform 0.5s 0s`
				}}>
				<BackgroundText
					text="Alex"
					incrementor={0} />
				<BackgroundText
					text="Figliolia"
					incrementor={0} />
				<div>
					<div>
						<h2>
							{
								'Contact'.split('').map((letter, i) => {
									return <div
													 style={{
													 	transition: `transform 0.5s ${i/20}s`
													 }} 
													 key={i}>{letter}</div>
								})
							}
						</h2>
					</div>
					<Button3D 
						text="Call Me"
						function={null} />
					<Button3D 
						text="Email Me"
						function={null} />
					<Button3D 
						text="GitHub"
						function={null} />
				</div>
			</section>
		);
	}
}


