import React, { PureComponent } from 'react';

export default class BackgroundText extends PureComponent {
	render = () => {
		let inc = this.props.incrementor;
		return (
			<h2>
				{
					this.props.text.split('').map((letter, i) => {
						inc += i;
						return (
							<div 
								key={i}
								style={{
									transition: `transform 0.5s ${2 + (inc/100)}s, 
															 opacity 0.5s ${2 + (inc/100)}s, 
															 color 0.3s 0s, text-shadow 0.3s 0s`
								}}>{letter}</div>
						);
					})
				}
			</h2>
		);
	}
}


