import React, { Component } from 'react';

export default class Slide extends Component {
  constructor(props) {
    super(props);
   	this.state = { infoClasses: "info" }
  }

  componentDidMount() {
  	if(this.props.index === 0) {
  		setTimeout(() => { this.refs[0].classList.add('slide-reset'); }, 1500);
  	}
  }

  componentWillReceiveProps(nextProps) {
  	if(nextProps.width >= 957 && this.state.infoClasses === "info change-info") {
  		this.setState({ infoClasses: "info" });
  	}
  	setTimeout(() => {
  		if(nextProps.selectedIndex === this.props.index) {
  			this.refs[this.props.index].classList.add('slide-reset');
  		} else {
  			this.refs[this.props.index].classList.remove('slide-reset');
  			this.setState({ infoClasses: "info" });
  		}
	  }, 1500);
  }

  visit = (e) => window.open(e.target.dataset.url, '_blank');

  switchText = () => {
  	this.setState((prevState, prevProps) => {
  		return {
  			infoClasses: prevState.infoClasses === "info" ? "info change-info" : "info"
  		}
  	});
  }

  render() {
    return (
    	<div
				ref={this.props.index}
				key={this.props.index}
				className="project"
				style={{
					backgroundPositionX: this.props.selectedIndex === this.props.index ? `${this.props.moveX}%` : '50%'
				}}>
				<div 
					id='snow' 
					style={{
						display: this.props.selectedIndex === 8 ? "block" : "none"
					}}></div>
				<div>
					<h2>
						{
							this.props.name.split('').map((letter, i) => {
								return (letter === ' ') ? <div key={i}>&nbsp;&nbsp;&nbsp;</div> : <div style={{transitionDelay: (0.3 + i/20) + 's'}} key={i}>{letter}</div>
							})
						}
					</h2>
					<div className={this.state.infoClasses}>
						<p>
							{this.props.p1}
							<a onClick={this.switchText}>&nbsp;More Info</a>
						</p>
						<p className={this.props.width < 957 ? "text-switch" : ""}>
							{this.props.p2}
							<a onClick={this.switchText}>&nbsp;Back</a>
						</p>
					</div>
					<button 
						onClick={this.visit} 
						data-url={this.props.url}>
						<h3 data-url={this.props.url}>Visit</h3>
					</button>
				</div>
			</div>
    );
  }
}



