import React, { PureComponent } from 'react';

export default class menu extends PureComponent {
	render = () => {
		const d = this.props.classes === "menu" ? 0 : 0.4;
		return (
			<section className={this.props.classes}>
				<div className="panel panel1"></div>
				<div className="panel panel2"></div>
				<div className='links'>
					<h2>
						{
							'Home'.split('').map((letter, i) => {
								return <div
													key={i} 
													onClick={this.props.nav} 
													data-page="Home"
													style={{
														transitionDelay: (d + (i/40)) + 's'
													}}>{letter}</div>
							})
						}
					</h2>
					<h2>
						{
							'Work'.split('').map((letter, i) => {
								return <div
													key={i}
													onClick={this.props.nav} 
													data-page="Work"
													style={{
														transitionDelay: (d + ((3-i)/40)) + 's'
													}}>{letter}</div>
							})
						}
					</h2>
					<h2>
						{
							'Contact'.split('').map((letter, i) => {
								return <div
													key={i}
													onClick={this.props.nav}
													data-page="Contact"
													style={{
														transitionDelay: (d + (i/40)) + 's'
													}}>{letter}</div>
							})
						}
					</h2>
				</div>
			</section>
		);
	}
}


