import React, { PureComponent } from 'react';
import Header from './components/header/Header';
import Home from './components/home/Home';
import Menu from './components/menu/Menu';
import Loader from './components/loader/Loader';
import Work from './components/work/Work';
import Contact from './components/contact/Contact';

export default class App extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      height: window.innerHeight,
      width: window.innerWidth,
      burgerClasses: "hamburglar is-open",
      burgerToggle: true,
      menuClasses: "menu",
      loaderClasses: "loader loader-show",
      homeClasses: "home",
      contactClasses: "home contact",
      page: "Home",
      pastInitLoad: false,
      moveX: null,
      transDur: '0s'
    }
  }

  componentDidMount(){
    window.addEventListener('resize', this.updateHeight);
    if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) && window.DeviceOrientationEvent){
      window.addEventListener('deviceorientation', this.updateMoveX, true);
    } 
    setTimeout(() => { this.setState({loaderClasses: "loader"});
      setTimeout(() => {
        this.setHomeClasses();
        setTimeout(() => { this.setState({ pastInitLoad: true }); }, 1000);
      }, 100);
    }, 500);
  }

  componentWillUnmount() {
    if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) && window.DeviceOrientationEvent){
      window.removeEventListener('deviceorientation', this.updateMoveX, true);
    } 
  }

  updateMoveX = (e) => this.setState({ moveX: ((e.gamma + 90) * 100) / 180 });

  updateHeight = () => this.setState({height: window.innerHeight, width: window.innerWidth });

  hideLoader = () => this.setState({ loaderClasses: "loader" });

  handlePageMove = (e) => {
    if(!window.DeviceOrientationEvent) {
      const x = e.type === 'mousemove' ? e.clientX : e.touches[0].clientX;
      const w = window.innerWidth;
      this.setState({ moveX: (x * 100) / w, transDur: '0s'});
    }
  }
  
  handlePageEnter = (e) => {
    if(!window.DeviceOrientationEvent) {
      const x = e.type === 'mouseenter' ? e.clientX : e.touches[0].clientX;
      const w = window.innerWidth;
      this.setState({ moveX: (x * 100) / w, transDur: '0.5s'});
    }
  }

  toggleBurger = () => {
    this.setState((prevState, prevProps) => {
      return {
        burgerToggle : !prevState.burgerToggle,
        burgerClasses : (prevState.burgerClasses === "hamburglar is-closed") ? 
                          "hamburglar is-open" : 
                          "hamburglar is-closed",
        menuClasses: (prevState.menuClasses === "menu") ?
                      "menu menu-show" :
                      "menu"
      }
    });
  }

  setHomeClasses = () => {
    setTimeout(() => {  this.setState({homeClasses: 'home home-show'}); }, 1000);
    setTimeout(() => { this.setState({homeClasses: 'home home-show home-reset-delays'}); }, 1500);
  }

  setContactClasses = () => {
    setTimeout(() => {  this.setState({contactClasses: 'home contact contact-show'}); }, 1000);
    setTimeout(() => { this.setState({contactClasses: 'home contact contact-show contact-reset-delays'}); }, 1500);
  }

  removeHomeClasses = () => {
    setTimeout(() => { this.setState({homeClasses: 'home'}) }, 1500);
  }

  removeContactClasses = () => {
    setTimeout(() => { this.setState({contactClasses: 'home contact'}) }, 1500);
  }

  navigate = (e) => {
    const p = e.target.dataset.page;
    if(p !== this.state.page && !this.state.burgerToggle) {
      this.toggleBurger();
      setTimeout(() => { this.setState({loaderClasses: "loader loader-show"}) }, 700);
      setTimeout(() => { this.setState({ page: p }) }, 1200);
    } else if(p !== this.state.page && this.state.burgerToggle) {
      this.setState({loaderClasses: "loader loader-show"});
      setTimeout(() => { this.setState({ page: p }) }, 500);
    }
  }

  render = () => {
    return (
      <div className="App" style={{height: this.state.height}}>
        <Loader 
          classes={this.state.loaderClasses}
          pastInitLoad={this.state.pastInitLoad} />
        <Header 
          burgerStuff={this.state.burgerClasses}
          burger={this.toggleBurger}
          nav={this.navigate} />

        {
          this.state.page === "Home" &&
          <Home
            classes={this.state.homeClasses}
            pastInitLoad={this.state.pastInitLoad}
            moveX={this.state.moveX}
            transDur={this.state.transDur}
            removeHomeClasses={this.removeHomeClasses}
            nav={this.navigate}
            loaded={this.setHomeClasses}
            hideLoader={this.hideLoader}
            handlePageMove={this.handlePageMove}
            handlePageEnter={this.handlePageEnter} />
        }

        {
          this.state.page === "Work" &&
          <Work
            hideLoader={this.hideLoader}
            moveX={this.state.moveX}
            transDur={this.state.transDur}
            width={this.state.width} />
        }
        
        {
          this.state.page === "Contact" &&
          <Contact
            classes={this.state.contactClasses}
            moveX={this.state.moveX}
            transDur={this.state.transDur}
            hideLoader={this.hideLoader}
            loaded={this.setContactClasses}
            removeContactClasses={this.removeContactClasses}
            handlePageMove={this.handlePageMove}
            handlePageEnter={this.handlePageEnter} />
        }
        <Menu 
          classes={this.state.menuClasses}
          nav={this.navigate} />
      </div>
    );
  }
}



