import React, { PureComponent } from 'react';
import Flickity from 'flickity';
import { api } from './workApi';
import Slide from './Slide';

export default class Work extends PureComponent {
	constructor(props) {
		super(props);
		this.state = {
			selectedIndex: 0,
			workApi: api,
			infoClasses: "info"
		}
	}
	componentDidMount() {
    const carousel = document.getElementById('slider');
    const options = {
      cellSelector: '.project',
      contain: true,
      initialIndex: 0,
      accessibility: true,
      prevNextButtons: true,
      wrapAround: true,
      pageDots: false,
      selectedAttraction: 0.15,
			friction: 0.8
    }
		this.ct = setTimeout(() => { 
			this.props.hideLoader();
			this.flkty = new Flickity(carousel, options);
	    this.flkty.on('cellSelect', () => {
	    	this.setState({ selectedIndex: this.flkty.selectedIndex });
	    });
		}, 500);
  }

  componentWillUnmount() {
    if (this.flkty) {
      this.flkty.off('cellSelect', this.updateSelected);
      this.flkty.destroy();
    }
    clearTimeout(this.ct);
  }

	render = () => {
		return (
			<section className="work" id='slider'>
				{
					this.state.workApi.map((obj, i) => {
						return (
							<Slide
								index={i}
								key={i}
								name={obj.name}
								selectedIndex={this.state.selectedIndex}
								moveX={this.props.moveX}
								width={this.props.width}
								url={obj.url}
								p1={obj.p1}
								p2={obj.p2} />
						);
					})
				}

			</section>
		);
	}
}


